import React from 'react'
import cn from 'classnames'
import style from './btn.module.scss'
export interface IButtonProps {
  children?: React.ReactNode
  onClick?: (e: any) => void
  styles?: {}
  className?: string
  href?: string
  target?: string
  size?: string
  variant?: string
}

const Button: React.SFC<IButtonProps> = props => {
  if (props.href) {
    return (
      <a
        className={
          props.className +
          ' ' +
          cn(
            style.btn__link,
            props.variant === 'primary' && style['btn--primary'],
            props.size === 'medium' && style['btn--medium'],
            props.size === 'small' && style['btn--small']
          )
        }
        href={props.href}
        target={props.target}
      >
        {props.children}
      </a>
    )
  }
  return (
    <button
      className={
        props.className +
        ' ' +
        cn(
          style.btn,
          props.variant === 'primary' && style['btn--primary'],
          props.variant === 'secondary' && style['btn--secondary'],
          props.size === 'medium' && style['btn--medium'],
          props.size === 'small' && style['btn--small']
        )
      }
      onClick={props.onClick}
    >
      {props.children}
    </button>
  )
}

export default Button

Button.defaultProps = {
  className: style.btn,
  children: 'I\'m default with my awesome emojï 😂',
  onClick: () => {},
}
